footer {
  background-color: var(--vy-dk-cyan);
}

.footer {
  padding-top: 8.625rem;
  padding-bottom: 2.9375rem;
  position: relative;
}
.footer-build {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.9375rem;
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: 90%;
  max-width: 50rem;
  transform: translate(-50%, 50%);
  border-radius: 0.625rem;
  box-shadow: 0 0.125rem 0.625rem #4a4a4a;
  padding: 1.25rem 0;
  background-color: var(--white);
  text-align: center;
}
@media (min-width: 40em) {
  .footer-build {
    gap: 2.375rem;
    height: 17.5rem;
  }
}
.footer-build span {
  display: block;
  font-size: 1.6875rem;
  font-family: var(--font-heading);
  font-weight: 600;
}
@media (min-width: 40em) {
  .footer-build span {
    font-size: 2.25rem;
  }
}
.footer-build a {
  display: inline-block;
  border-radius: 6.25rem;
  padding: 1rem 1.25rem;
  color: var(--white);
  background-color: var(--pink);
  font-weight: 700;
  font-size: 0.875rem;
  transition: all 300ms ease-in;
}
@media (min-width: 40em) {
  .footer-build a {
    font-size: 1.125rem;
    padding: 1.5625rem 6.25rem;
  }
}
.footer-build a:hover {
  color: var(--pink);
  background-color: var(--black);
}
.footer-logo {
  display: inline-block;
  margin-bottom: 1.75rem;
}
.footer-logo img {
  width: 10.5rem;
  height: 1.75rem;
}
@media (min-width: 71.875em) {
  .footer-logo img {
    width: 15.3125rem;
    height: 2.5rem;
  }
}
.footer-copyright {
  color: var(--white);
  font-size: 0.8125rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .footer-copyright {
    font-size: 0.9375rem;
    text-align: right;
  }
}

.info {
  color: var(--white);
  font-size: 1rem;
}
.info__contact {
  margin-bottom: 3.875rem;
  font-size: 1.25rem;
}
.info__contact > *:not(:last-child) {
  margin-bottom: 1.75rem;
}
.info__contact div {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5625rem;
}
.info__contact--loc {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--loc ~ span {
  color: var(--white);
  align-self: flex-start;
}
.info__contact--num {
  color: var(--white);
}
.info__contact--email {
  color: var(--white);
}
.info__links {
  margin-top: 0;
  margin-bottom: 3.5625rem;
  padding: 0;
  list-style-type: none;
  font-size: 1.125rem;
}
.info__links > *:not(:last-child) {
  margin-bottom: 1.75rem;
}